@use "sass:map";

.statistic {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 121px;
  margin-top: 22px;

  @include mobile {
    margin-top: 13px;
    margin-bottom: 50px;
  }

  /**** statistic headline  ****/
  &__headline {
    font-family: $font-raleway;
    font-weight: 400;
    font-size: 2.25rem;
    line-height: 2.625rem;
    color: map.get($colors, color-black);
    text-align: center;
    margin-bottom: 101px;

    @include mobile {
      margin-bottom: 25px;
    }
  }

  /**** statistic columns  ****/
  &__columns {
    max-width: 800px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 185px;

    @include mobile {
      flex-direction: column;
      gap: 25px;
    }

    .column {
      display: flex;
      flex-direction: column;
      align-items: center;

      .circle {
        display: flex;
        justify-content: center;
        width: 63px;
        height: 63px;
        border-radius: 50%;
        margin-bottom: 14.2px;
        position: relative;

        &__red {
          background-color: map.get($colors, color-redish);
          .icon__person {
            position: absolute;
            top: 4px;
            left: 46%;
          }
        }

        &__blue {
          background-color: map.get($colors, color-primary);
          .icon__eye {
            position: absolute;
            top: 14px;
            left: 32%;
          }
        }

        &__green {
          background-color: map.get($colors, color-green);
          .icon__catalog {
            position: absolute;
            top: 6px;
            left: 50%;
          }
        }
      }

      &__number {
        font-family: $font-raleway;
        font-weight: 800;
        font-size: 1.25rem;
        line-height: 1.5rem;
        letter-spacing: 0.375rem;
        margin-bottom: 19.06px;
      }

      &__text {
        font-family: $font-raleway;
        font-weight:400;
        font-size: 1.0625rem;
        line-height: 1.25rem ;
        color: map.get($colors, color-grey);
        text-align: center;
      }

      &__centre--text {
        font-size: 1rem;
        line-height: 1.1875rem;
      }
    }
  }
}